

























import _ from 'lodash';
import { Component, Vue, Watch } from 'vue-property-decorator'
// import {Table,FilterItem,Pagination,Dialog} from '@/components/views/oss/img/manage'
import { Table,FilterItem,Pagination,Dialog } from '@/pages/adminWeb/components/views/oss/file'
import axios from 'axios'
@Component({
  // components:{Table,FilterItem,Pagination,Dialog}
  components: { Table,FilterItem,Pagination,Dialog }
})
export default class ViewComponent extends Vue {
  /* data */
  direct = 'api/file' // 请求地址
  // 表格模块
  propTable = {
    tableData: [],
    direct: this.direct, 
  }
  //- 筛选模块
  propFilterItem = {
    ruleForm: {
      name: '',
      collected__date__gte: '',
      collected__date__lte: '',
    },
    active: false,
    direct: this.direct, // 模糊搜索时会用到 
  }
  //- 分页模块
  propPagination = {
    pagination: {
      page_index: 1, // 当前位于哪页
      total: 0, // 总数
      page_size: 5, // 一页显示多少条
      page_sizes: [5, 10, 15, 20], // 每页显示多少条
      layout: 'total,sizes,prev,pager,next,jumper', // 翻页属性
    }
  }

  //- 弹窗
  isShowDialog = false
  propDialog = {}

  /* lifecycle hook */
  created() {
    // 初始化页面数据：
    this.requestApi() // 采用默认分页参数监听函数，立即加载一次
  }

  /* method */
  // 监听:表格模块
  emitTable(response: any) {
    // 点击“删除”按钮
    if (response.method == 'delete') {
      this.requestApi(response);
      console.log('response',response)
    }
    // 点击“编辑”按钮
    if (response.method == 'patch') {
      if (response.mode == 'editInline') { // 如果是行内编辑直接发送更新请求
        delete response.mode
        this.requestApi(response);
        return;
      }

      this.propDialog = response
      this.isShowDialog = true
    }
  }
  // 监听：验证筛选模块
  emitFilterItem(response: any) {
    this.propPagination.pagination.page_index = 1; // 切换为第一分页
    // 点击“查询”按钮
    if (response.method === 'get') {
      const data = response.data;
      this.requestApi({
        data
      });
    }
    // 点击“新增”按钮
    // 监听：弹窗（新增&编辑）
    if (response.method === 'post') {
      this.propDialog = response
      this.isShowDialog = true
    }
  }
  // 监听：分页操作,触发分页参数监听函数
  emitPagination(response: any) {
    this.requestApi()
  }
  // 监听：弹窗（新增&编辑&上传）
  emitDialog(response: any) {
    // 处理条目
    const data = _.cloneDeep(response);
    data.edit = false;
    delete data.uid;
    delete data.status;
    // 处理数组
    const cloneTableData:any = _.cloneDeep(this.propTable.tableData);
    cloneTableData.unshift(data);
    this.propTable.tableData = [];
    setTimeout(()=>{
      this.propTable.tableData = cloneTableData;
    });

    // (this.propTable.tableData as any).unshift(response)

    // if (response.method === 'post') { // 新增模式下
    //   this.propPagination.pagination.page_index = 1; // 切换为第一分页
    //   this.propFilterItem.active = false // 关闭筛选激活参数,使切换分页时不带筛选参数
    // }

    // this.requestApi(response)
  }

  async requestApi(conf: any = {}) {
    const {
      data = {},
      method = 'get',
      index = 0,
    } = conf
    const id = data._id || conf.id || '';
    const idVal = id ? '/' + id : '';
    Object.assign(data, this.propPagination) // 加入分页参数
    if (this.propFilterItem.active === true) {
      data.filterFields = this.propFilterItem.ruleForm // 加入筛选参数
    }
    // console.log('data',data)

    const response = await this.$request({
      direct: this.direct + idVal,
      data,
      method,
    })
    // console.log('response:',response)

    
    
    if (response.pagination) {
      this.propPagination.pagination.total = response.pagination.total
    }

    if (method === 'post') { // 增
      // const index = this.fileList.length-1;
      // const newChild:any = this.fileList[index];
      // newChild['_id'] = response._id;
      // this.fileList[index] = newChild;
    }
    else if (method == 'delete') { // 删 *待定：是否也后台拉取数据更新页面
      const cloneTableData:any = _.cloneDeep(this.propTable.tableData);
      cloneTableData.splice(index, 1);
      this.propTable.tableData = []; // 打断一下，兼容图片组件不能实时更新的情况
      setTimeout(()=>{
        this.propTable.tableData = cloneTableData
      })
      // this.propTable.tableData.splice(index, 1); // 页面展示删除一条


      this.propPagination.pagination.total--; // 总条数减一
      this.$message({
        message: '删除成功',
        type: 'success'
      })
    }
    else if(method=='patch'){ // 改
      this.$message({
        message:'修改成功',
        type: 'success'
      })
      // const cloneTableData:any = _.cloneDeep(this.propTable.tableData);
      // cloneTableData[index] = response
      // cloneTableData[index].edit = false
      // this.propTable.tableData = []; // 打断一下，兼容图片组件不能实时更新的情况
      // setTimeout(()=>{
      //   this.propTable.tableData = cloneTableData
      // })

      // 此处不采用硬刷，还可以做到替换后，不被还原，否则url加载资源不刷新页面无法更新
      const cloneTableData:any = _.cloneDeep(this.propTable.tableData);
      cloneTableData[index] = response
      cloneTableData[index].edit = false
      this.propTable.tableData = cloneTableData
    }
    else if (method == 'get') { // 查
      response.results.forEach((item: any) => {
        // 设置编辑状态
        item.edit = false
      })
      this.propTable.tableData = []; // 打断一下，兼容图片组件不能实时更新的情况
      setTimeout(()=>{
        this.propTable.tableData = response.results
      })
    }
    
  }
}
