


























import _ from 'lodash';
import clip from '@/utils/clipboardWeb.ts' // use clipboard directly
import { Component, Vue, Prop } from 'vue-property-decorator'
@Component({
  components: {}
})
export default class ViewComponent extends Vue {
  /* prop */
  @Prop() prop!: any; // 父组件传值
  /**data */
  imageUrl = this.prop.file.url
  uploadImgAction = `${this.$store.state.domainNodeServer}/${this.prop.direct}/${this.prop.file._id}`
  uploadImgData = {
    uploader: this.$store.state.user.name || '',
    url: this.prop.file.url,
    lastTimeModify: this.prop.lastTimeModify,
  }

  // 上传进度条相关
  isShowProgress = false
  progressPercent = 0

  /**method */
  handleAvatarSuccess(res:any, file:any) {
    this.imageUrl = URL.createObjectURL(file.raw);
    // console.log('URL.createObjectURL(file.raw)---',URL.createObjectURL(file.raw))

    // 更新父组件视图
    this.$emit('emit',{
      syncImg:URL.createObjectURL(file.raw),
    })

    // 关闭进度条(修复进度闪现过快，没开始转动就消失问题)
    this.progressPercent = 100;
    setTimeout(()=>{
      this.isShowProgress = false;
      // this.progressPercent = 0;
    },600)

    // 上传成功弹窗
    this.$message({
      message: '上传成功',
      type: 'success'
    })
  }
  handleError(response: any, file: any, fileList: any) {
    console.log('error response',response)
  }

  // 上传
  handleProcess(event:any, file:any, fileList:any){
    this.isShowProgress = true;
    this.progressPercent = +file.percentage.toFixed(0);
  }
}
