








































import _ from 'lodash';
import clip from '@/utils/clipboardWeb.ts' // use clipboard directly
import { Component, Vue, Prop } from 'vue-property-decorator'
@Component({
  components: {}
})
export default class ViewComponent extends Vue {
  /* prop */
  @Prop() prop!: any; // 父组件传值
  /**data */
  dialogImageUrl = ''
  dialogVisible = false
  uploadImgAction = `${this.$store.state.domainNodeServer}/${this.prop.direct}`
  uploadImgData = {
    project: 'common',
    uploader: this.$store.state.user.name || '',
  }
  fileList: object[] = []
  disabled = false
  // 辅助静态展示信息
  optionProject = [
    {
      key: 'common',
      role: '通用',
    },
    {
      key: 'msf',
      role: '小微社交金融',
    },
    {
      key: "dkd",
      role: "道口贷",
    },
    {
      key: "dky",
      role: "道口云",
    },
  ]
  // 上传进度条相关
  isShowProgress = false
  progressPercent = 0

  /**life hook */
  created() {
    // this.requestApi();
  }


  /**method */
  handleSuccess(response: any, file: any, fileList: any) {
    const data = response
    // 增
    this.$message({
      message: '成功',
      type: 'success'
    })
    data.uid = file.uid;
    this.fileList.push(data)
    // 更新表格
    const index = this.fileList.length-1;
    const newChild:any = this.fileList[index];
    newChild['_id'] = response._id;
    this.fileList[index] = newChild;
    // 更新表格
    this.$emit(
      'emit',
      response // 发射子组件参数
    )
    // 关闭进度条(修复进度闪现过快，没开始转动就消失问题)
    this.progressPercent = 100;
    setTimeout(()=>{
      this.isShowProgress = false;
      // this.progressPercent = 0;
    },600)
  }
  handleDownload(file: any, event: any) {
    const data = file.response
    if(data){
      clip(data.url, event)
    }else{
      clip(file.url, event)  
    }
  }
  handleError(response: any, file: any, fileList: any) {
    console.log('error response',response)
  }
  // handleRemove(file: any, fileList: any) {
  //   console.log('file',file)
  //   this.fileList = this.fileList.filter((v: any) => {
  //     console.log(file.uid,v.uid)
  //     return file.uid !== v.uid
  //   })
  //   this.requestApi({
  //     id: file._id,
  //     method:'delete',
  //   })
  // }
  handlePictureCardPreview(file: any) {
    this.dialogImageUrl = file.url;
    this.dialogVisible = true;
  }
  // 上传
  handleProcess(event:any, file:any, fileList:any){
    this.isShowProgress = true;
    this.progressPercent = +file.percentage.toFixed(0);
  }
}
