



























































import {debounce} from '@/utils'
import {Component,Vue,Prop,Watch} from 'vue-property-decorator'
@Component({
  components:{}
})
export default class ViewComponent extends Vue {
  /* prop */
  @Prop() prop!: any; // 父组件传值

  /* data */
  ruleForm = {
    name:'',
    collected__date__gte:'',
    collected__date__lte:'',
    project:'',
    env:{
      prod:true,
      dev:true,
    },
    sortByTime:'时间倒序',
    uploader:'',
    url:'',
  }
  @Watch('ruleForm.url',{immediate: false}) onUrlChange(val: string, oldVal: string) { 
    this.debounceUrlFilter()
  }
  debounceUrlFilter = function () {};
  rules = {}
  // 辅助信息
  allName = []
  timeout:any = null
  // 辅助静态展示信息
  optionProject = [
    {
      key: 'common',
      role: '通用',
    },
    {
      key: 'msf',
      role: '小微社交金融',
    },
    {
      key: "dkd",
      role: "道口贷",
    },
    {
      key: "dky",
      role: "道口云",
    },
  ]
  optionRole = [
    {
      key: 'admin',
      role: '管理员',
      des: 'Super Administrator. Have access to view all pages.',
    },
    {
      key: "editor",
      role: "编辑",
      des: "Normal Editor. Can see all pages except permission page"
    },
    {
      key: "visitor",
      role: "游客",
      des: "Just a visitor. Can only see the home page and the document page"
    },
  ]

  /* lifecycle hook */
  created(){
    this.debounceUrlFilter = debounce(()=>{
      this.submitForm('ruleForm')
    },800)
  }

  /* method */
  submitForm(formName: string) { // 表单提交校验
    (this.$refs[formName] as any).validate(
      (valid: boolean) => {
        if (valid) {
          this.$emit('update:prop',{
            ruleForm:this.ruleForm,
            active: true,
            direct: this.prop.direct,
          })
          this.emit({ // 发射子组件参数
            /* 请求返回的数据 */
            data: {
              filterFields:this.ruleForm
            },
            /* 其他控制字段 */
            method:'get'
          })  
        }
      }
    )
  }
  handleAdd(){
    this.emit({ // 发射子组件参数
      /* 请求返回的数据 */
      /* 其他控制字段 */
      method:'post',
      title:'新增',
      direct: this.prop.direct,
    })
  }
  /* 向父组件发射值 */
  emit(response: object) {
    this.$emit(
      'emit',
      response // 发射子组件参数
    )
  }
  // 模糊搜索相关
  querySearchAsync(queryString:string, cb?:any) {
    // var queryData = this.allName;
    // var results = queryString ? queryData.filter(this.createStateFilter(queryString)) : queryData;

    clearTimeout(this.timeout);
    this.timeout = setTimeout(async () => {
      // cb(results);
      const response = await this.$request({
        // api:'user',
        direct: this.prop.direct,
        data:{
          querySearchField:'name',
          querySearchValue:this.ruleForm.name,
        },
        method:'get',
      })

      // if(response.status >= 200 && response.status < 400){
        this.allName = response.results
        const queryData = this.allName;
        const results = queryString ? queryData.filter(this.createStateFilter(queryString)) : queryData;
        cb(results);
      // }
    }, 1000 * Math.random());
  }
  createStateFilter(queryString: string) {
    return (state:any) => {
      return (state.value.toString().toLowerCase().indexOf(queryString.toLowerCase()) === 0);
    };
  }
}
